<template>
    <scroll-view class="page-scroll" :scroll-y="needScroll" :style="scrollStyle">
      <view class="card-container" :style="{ paddingTop: navigationBarHeight + 50 + 'px' }">
        <!-- 寄件人收件人卡片 -->
        <view class="card">
          <view class="card-row">
            <view class="card-info" @tap="goToAddressBook('send')">
              <view class="name-phone">
                <text class="text name">{{ senderInfo.name || '请添加寄件人信息' }}</text>
                <text class="text phone" v-if="senderInfo.phone">{{ senderInfo.phone }}</text>
              </view>
              <view class="address-line">
                <text class="card-label">寄</text>
                <text class="text address" :class="{'placeholder': !senderInfo.address}">
                  {{ senderInfo.address || '暂无寄件人信息，点击填写' }}
                </text>
              </view>
            </view>
            <view class="card-action send" @tap.stop="goToAddressBook('send')">
              <text>地址簿</text>
            </view>
          </view>
          <view class="divider"></view>
          <view class="card-row">
            <view class="card-info" @tap="goToAddressBook('receive')">
              <view class="name-phone">
                <text class="text name">{{ receiverInfo.name || '请添加收件人信息' }}</text>
                <text class="text phone" v-if="receiverInfo.phone">{{ receiverInfo.phone }}</text>
              </view>
              <view class="address-line">
                <text class="card-label receive">收</text>
                <text class="text address" :class="{'placeholder': !receiverInfo.address}">
                  {{ receiverInfo.address || '暂无收件人信息，点击填写' }}
                </text>
              </view>
            </view>
            <view class="card-action receive" @tap.stop="goToAddressBook('receive')">
              <text>地址簿</text>
            </view>
          </view>
        </view>
  
        <!-- 物品信息卡片 -->
        <view class="card item-card" @tap="showItemPopup">
          <view class="item-row">
            <text class="item-label">物品信息</text>
            <view class="item-action">
              <text :class="{'placeholder-text': !selectedItemInfo, 'selected-text': selectedItemInfo}">
                {{ formattedItemInfo }}
              </text>
              <uni-icons type="right" size="16" color="#999999"></uni-icons>
            </view>
          </view>
        </view>
  
        <!-- 快递公司选择卡片 -->
        <view class="card company-card">
          <text class="company-title">选择快递公司</text>
          <scroll-view class="company-scroll" scroll-x>
            <view class="company-list">
              <view class="company-item" :class="{ active: selectedCompany === 'sf' }" @tap="selectCompany('sf')">
                <text class="recommend-tag">推荐</text>
                <view class="company-header">
                  <image class="company-logo" src="/static/icons/sf.png" mode="aspectFit"></image>
                  <view class="company-main">
                    <text class="company-name">顺丰重货</text>
                    <view class="price-info">
                      <text class="current-price">¥30.0<text class="price-unit">起</text></text>
                      <text class="original-price">¥40.0</text>
                    </view>
                  </view>
                </view>
                <text class="weight-info">续重 15元/kg</text>
                <view v-if="selectedCompany === 'sf'" class="check-icon-wrapper">
                  <text class="check-icon">✓</text>
                </view>
              </view>
  
              <view class="company-item" :class="{ active: selectedCompany === 'db' }" @tap="selectCompany('db')">
                <view class="company-header">
                  <image class="company-logo" src="/static/icons/db.png" mode="aspectFit"></image>
                  <view class="company-main">
                    <text class="company-name">德邦快递</text>
                    <view class="price-info">
                      <text class="current-price">¥25.0<text class="price-unit">起</text></text>
                      <text class="original-price">¥35.0</text>
                    </view>
                  </view>
                </view>
                <text class="weight-info">续重 12元/kg</text>
                <view v-if="selectedCompany === 'db'" class="check-icon-wrapper">
                  <text class="check-icon">✓</text>
                </view>
              </view>
            </view>
          </scroll-view>
        </view>
  
        <!-- 上门时间和保费卡片 -->
        <view class="card time-card">
          <view class="time-row">
            <text class="time-label">期望上门时间</text>
            <view class="time-action" @tap="showPickupTimePopup">
              <text :class="{'placeholder-text': !pickupTime, 'selected-text': pickupTime}">
                {{ pickupTime || '请选择上门时间' }}
              </text>
              <uni-icons type="right" size="16" color="#999999"></uni-icons>
            </view>
          </view>
          <view class="insurance-row" v-if="pickupTime">
            <text class="insurance-label">保费</text>
            <view class="insurance-info" @tap="showInsurancePopup">
              <text class="insurance-optional">选填</text>
              <uni-icons type="right" size="16" color="#999999"></uni-icons>
            </view>
          </view>
        </view>
      </view>
  
      <!-- 底部结算导航 -->
      <view class="footer">
        <view class="footer-left">
          <view class="price-section">
            <text class="price-label">预计：</text>
            <text class="price-value">¥30.0</text>
            <view class="price-detail" @tap="toggleDetailDrawer">
              <text>明细</text>
              <uni-icons :type="showDetail ? 'top' : 'bottom'" size="12" color="#007AFF"></uni-icons>
            </view>
          </view>
          <view class="agreement">
            <checkbox :checked="isAgree" @tap="toggleAgree" color="#007AFF" style="transform:scale(0.7)"/>
            <text class="agreement-text">
              阅读并同意
              <text class="agreement-link">《快递服务协议》</text>
            </text>
          </view>
        </view>
        <view class="footer-right">
          <button class="submit-btn">立即下单</button>
        </view>
      </view>
  
      <!-- 明细抽屉 -->
      <view class="detail-drawer" v-if="showDetail">
        <view class="drawer-mask" @tap="closeDetailDrawer"></view>
        <view class="drawer-content">
          <view class="drawer-header">
            <text class="drawer-title">明细</text>
            <uni-icons type="closeempty" size="20" color="#999" @tap="closeDetailDrawer"></uni-icons>
          </view>
          <view class="drawer-body">
            <view class="detail-item">
              <text>首重(5kg)</text>
              <text>¥{{ basePrice }}</text>
            </view>
            <view class="detail-item">
              <text>续重</text>
              <text>¥{{ extraPrice }}/kg × {{ extraWeight }}kg = ¥{{ totalExtraPrice }}</text>
            </view>
          </view>
        </view>
      </view>
  
      <!-- 弹出层组件 -->
      <InsurancePopup ref="insurancePopup" @confirm="onInsuranceConfirm" />
      <pickup-time-popup ref="pickupTimePopup" @confirm="onPickupTimeConfirm" />
      <item-popup ref="itemPopup" @confirm="onItemConfirm" />
    </scroll-view>
  </template>
  
  <script>
  import deviceMixin from '@/mixins/device.js'
  import InsurancePopup from './popups/insurance-popup.vue'
  import PickupTimePopup from './popups/pickup-time-popup.vue'
  import ItemPopup from './popups/item-popup.vue'
  
  export default {
    name: 'ExpressLarge',
    props: {
      navHeight: {
        type: Number,
        default: 0
      }
    },
    mixins: [deviceMixin],
    components: {
      InsurancePopup,
      PickupTimePopup,
      ItemPopup
    },
    data() {
      return {
        selectedCompany: '',
        isAgree: false,
        selectedType: '',
        weight: 5, // 默认5kg起
        remark: '',
        itemDetail: '',
        selectedItemInfo: '',
        pickupTime: '',
        declaredValue: '',
        insuranceFee: '1.00',
        agreeInsurance: false,
        senderInfo: {
          name: '',
          phone: '',
          address: ''
        },
        receiverInfo: {
          name: '',
          phone: '',
          address: ''
        },
        showDetail: false,
        basePrice: 30.0, // 首重价格
        extraPrice: 15.0, // 续重单价
        extraWeight: 0, // 续重重量
      }
    },
    computed: {
      scrollStyle() {
        return {
          height: this.needScroll ? '100vh' : 'auto'
        }
      },
      containerStyle() {
        return {
          'margin-top': this.marginTopStyle
        }
      },
      formattedItemInfo() {
        if (!this.selectedItemInfo) {
          return '请选择物品信息'
        }
        return `${this.selectedItemInfo}/${this.weight}kg`
      },
      totalExtraPrice() {
        return (this.extraPrice * this.extraWeight).toFixed(1)
      },
      canConfirmInsurance() {
        return this.declaredValue && this.agreeInsurance
      }
    },
    methods: {
      showItemPopup() {
        this.$refs.itemPopup.open()
      },
      onItemConfirm(data) {
        this.selectedItemInfo = data.itemDetail
        this.weight = data.weight
        this.calculatePickupTime()
      },
      selectCompany(company) {
        this.selectedCompany = company
      },
      showInsurancePopup() {
        this.$refs.insurancePopup.open()
      },
      onInsuranceConfirm(data) {
        console.log('保价信息：', data)
      },
      showPickupTimePopup() {
        this.$refs.pickupTimePopup.open()
      },
      onPickupTimeConfirm(time) {
        this.pickupTime = time
      },
      calculatePickupTime() {
        const now = new Date()
        const currentHour = now.getHours()
        
        if (currentHour >= 18) {
          const tomorrow = new Date(now.getTime() + 24 * 60 * 60 * 1000)
          this.pickupTime = `明天16:00-19:00`
        } else {
          this.pickupTime = `今天16:00-19:00`
        }
      },
      toggleDetailDrawer() {
        this.showDetail = !this.showDetail
      },
      closeDetailDrawer() {
        this.showDetail = false
      },
      toggleAgree() {
        this.isAgree = !this.isAgree
      },
      goToAddressBook(type) {
        uni.navigateTo({
          url: `/pages/address/address?type=${type}`,
          events: {
            selectAddress: (address) => {
              console.log('选择的地址:', address)
              if (address.type === 'send') {
                this.senderInfo = {
                  name: address.name,
                  phone: address.phone,
                  address: address.address
                }
              } else if (address.type === 'receive') {
                this.receiverInfo = {
                  name: address.name,
                  phone: address.phone,
                  address: address.address
                }
              }
            }
          }
        })
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.checkNeedScroll()
      })
    }
  }
  </script>
  
  <style>
  .page-scroll {
    flex: 1;
  }
  
  .card-container {
    padding: 0 20rpx;
    padding-bottom: 140rpx;
    position: relative;
    z-index: 1;
  }
  
  .card {
    background-color: #FFFFFF;
    border-radius: 16rpx;
    box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.1);
    padding: 30rpx;
    margin-bottom: 20rpx;
    height: 300rpx;
    display: flex;
    flex-direction: column;
  }
  
  .card-row {
    display: flex;
    align-items: flex-start;
    position: relative;
    padding: 10rpx 0;
    flex: 1;
  }
  
  .divider {
    height: 2rpx;
    background-color: #EEEEEE;
    margin: 0;
    width: 100%;
    flex-shrink: 0;
  }
  
  .card-info {
    flex: 1;
    min-width: 0;
    position: relative;
    padding: 10rpx 0;
    cursor: pointer;
  }
  
  .name-phone {
    display: flex;
    align-items: center;
    margin-bottom: 12rpx;
    padding-left: 70rpx;
  }
  
  .name-phone .text {
    font-size: 32rpx;
    color: #333333;
  }
  
  .name {
    font-weight: bold;
    margin-right: 20rpx;
  }
  
  .phone {
    color: #666666;
    font-weight: bold;
  }
  
  .address-line {
    display: flex;
    align-items: flex-start;
    position: relative;
  }
  
  .card-label {
    position: absolute;
    left: 0;
    top: -25rpx;
    font-size: 34rpx;
    font-weight: bold;
    color: #FFFFFF;
    width: 40rpx;
    height: 40rpx;
    text-align: center;
    line-height: 40rpx;
    background-color: #007AFF;
    border-radius: 6rpx;
  }
  
  .card-label.receive {
    background-color: #007AFF;
  }
  
  .address {
    font-size: 28rpx;
    color: #999999;
    width: 16em;
    word-wrap: break-word;
    white-space: pre-wrap;
    line-height: 1.4;
    padding-left: 70rpx;
  }
  
  .placeholder {
    color: #999999;
  }
  
  .card-action {
    font-size: 28rpx;
    color: #007AFF;
    font-weight: bold;
    padding-left: 30rpx;
    white-space: nowrap;
    position: relative;
    margin-top: 40rpx;
    border-left: 1px solid #EEEEEE;
    margin-left: 20rpx;
  }
  
  .card-action.send {
    margin-top: 35rpx;
  }
  
  .card-action.receive {
    margin-top: 35rpx;
  }
  
  .item-card {
    height: auto;
    padding: 30rpx;
  }
  
  .item-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10rpx 0;
  }
  
  .item-label {
    font-size: 32rpx;
    color: #333333;
  }
  
  .item-action {
    display: flex;
    align-items: center;
  }
  
  .placeholder-text {
    font-size: 28rpx;
    color: #999999;
    margin-right: 10rpx;
  }
  
  .selected-text {
    font-size: 28rpx;
    color: #333333;
    font-weight: bold;
    margin-right: 10rpx;
  }
  
  .company-card {
    height: auto;
    padding: 30rpx;
  }
  
  .company-title {
    font-size: 32rpx;
    color: #333333;
    font-weight: bold;
    margin-bottom: 20rpx;
  }
  
  .company-scroll {
    width: 100%;
    white-space: nowrap;
  }
  
  .company-list {
    display: flex;
    padding: 10rpx 0;
    gap: 20rpx;
  }
  
  .company-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300rpx;
    height: 200rpx;
    padding: 20rpx;
    border-radius: 12rpx;
    background-color: #FFFFFF;
    position: relative;
    flex-shrink: 0;
    border: 2rpx solid #EEEEEE;
  }
  
  .company-header {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 4rpx 0;
  }
  
  .company-logo {
    width: 80rpx;
    height: 80rpx;
    margin-right: 20rpx;
    flex-shrink: 0;
  }
  
  .company-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
  
  .company-name {
    font-size: 28rpx;
    color: #333333;
    font-weight: bold;
    margin-bottom: 8rpx;
  }
  
  .price-info {
    display: flex;
    align-items: center;
    gap: 8rpx;
  }
  
  .current-price {
    font-size: 32rpx;
    color: #FF4D4F;
    font-weight: bold;
  }
  
  .price-unit {
    font-size: 24rpx;
    font-weight: normal;
    margin-left: 2rpx;
  }
  
  .original-price {
    font-size: 24rpx;
    color: #999999;
    text-decoration: line-through;
  }
  
  .weight-info {
