    border-top: 1rpx solid #F5F5F5;
    margin-top: 10rpx;
  }
  
  .insurance-info {
    display: flex;
    align-items: center;
    gap: 12rpx;
  }
  
  .insurance-optional {
    font-size: 28rpx;
