    bottom: 0;
    height: 120rpx;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30rpx;
    box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);
    z-index: 2;
  }
  
  .footer-left {
    flex: 1;
  }
  
