    color: #333333;
  }
  
  .price-value {
    font-size: 36rpx;
    color: #FF4D4F;
    font-weight: bold;
  }
  
  .price-detail {
    display: flex;
    align-items: center;
    font-size: 24rpx;
    color: #007AFF;
    margin-left: 12rpx;
    padding: 2rpx 8rpx;
  }
  
  .agreement {
    display: flex;
    align-items: center;
  }
  
  .agreement-text {
    font-size: 24rpx;
    color: #666666;
  }
  
  .agreement-link {
    color: #007AFF;
  }
  
  .footer-right {
    margin-left: 20rpx;
  }
  
  .submit-btn {
    background: linear-gradient(to right, #FF9500, #FF5E3A);
    color: #FFFFFF;
    font-size: 32rpx;
    font-weight: bold;
    height: 80rpx;
    line-height: 80rpx;
    padding: 0 60rpx;
    border-radius: 40rpx;
    border: none;
  }
  
  /* 明细抽屉样式 */
  .detail-drawer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 1;
  }
  
  .drawer-mask {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
  }
  
  .drawer-content {
    position: absolute;
    left: 0;
